<template>
  <div>
    <div class="row no-gutters">
      <div class="col-lg-9">
        <div class="video-show-outer">
          <div class="video-show">
            <video id="vid1" class="video-js vjs-default-skin"></video>
          </div>
        </div>
      </div>
      <div class="col-lg-3">
        <div
          class="video-details"
          v-for="(items, index) in videoDetails"
          :key="index"
        >
          <span>{{ items.videoTitle }}</span>
          <span>觀看次數：{{ items.videoViewsTimes }}</span>
          <span>Channel：{{ items.videoChannelTitle }}</span>
          <span v-html="items.videoDesc"></span>
        </div>
      </div>
    </div>
  </div>
</template>
<style lang="scss" scoped>
.video-show-outer {
  margin: 10px;
  .video-show {
    overflow: hidden;
    transform: translate3d(0, 0, 0);
    border-radius: 20px;
    height: 95vh;
    box-shadow: 0 0 5px 1px rgba(0, 0, 0, 0.7);
    #vid1 {
      width: 100%;
      height: 100%;
    }
    .vjs-big-play-button-reset {
      top: 0;
    }
  }
  @media screen and (max-width: 414px) {
    .video-show {
      height: 26.7vh;
    }
  }
}
