  display: flex;
  flex-direction: column;
  margin: 10px;
  line-height: 35px;
  padding: 8px 15px;
  height: 95vh;
  background-color: rgba(0, 0, 0, 0.7);
  color: white;
  border-radius: 20px;
  box-shadow: 0 0 5px 1px rgba(0, 0, 0, 0.7);
  span {
    display: block;
  }
  span:nth-of-type(1) {
    width: 100%;
    font-size: 22px;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical;
  }
  span:nth-of-type(2) {
    font-size: 20px;
  }
  span:nth-of-type(3) {
    font-size: 18px;
  }
  span:nth-of-type(4) {
    box-shadow: inset 0 2px 0 0 rgba(255, 255, 255, 0.7);
    overflow: hidden;
