<script>
import videoJS from "video.js";
// import $ from "jquery";
export default {
  data() {
    return {
      urlTemp: "",
      videoDetails: [],
    };
  },
  mounted() {
    setTimeout(() => {
      if (this.urlTemp != undefined) {
        this.videoDetails = this.$route.params.videoItem;
        this.getPostId();
      } else {
        alert("請點選影片連結");
        this.$router.push("/");
      }
    }, 300);
  },
  methods: {
    getPostId() {
      videoJS(
        "#vid1",
        {
          controls: true,
          techOrder: ["youtube"],
          sources: [{ type: "video/youtube", src: this.urlTemp }],
          plugins: {
            videoJsResolutionSwitcher: {
              default: "high",
              dynamicLabel: true,
            },
          },
          loop: true,
          muted: true,
        },
        function () {
          var player = this;
          player.on("resolutionchange", function () {
            console.log(this);
            console.info("Source changed");
          });
        }
      );
    },
  },
  destroyed() {
    videoJS("#vid1").dispose();
  },
  created() {
    this.urlTemp = this.$route.params.postUrl;
  },
};
</script>