      searchText: "",
      rwdState: false,
      headerAnimate: false,
      scrollTemp: "",
      searBarAnimate: false,
    };
  },
  watch: {
    rwdState(status) {
      if (status == true) {
        document.body.style.paddingTop = `${
          this.domSelector(".header-rwd").offsetHeight
        }px`;
        document.body.style.paddingBottom = `${
          this.domSelector(".nav-bar-rwd").offsetHeight
        }px`;
      }
    },
  },
  methods: {
    domSelector(el) {
      return document.querySelector(el);
    },
    transToPartail() {
      this.$children[2].getSearch("", this.searchText);
    },
    currentLink(currentText) {
      this.headerAnimate = currentText == "collect" ? true : false;
      this.headerAnimate == true
        ? (document.body.style.paddingTop = "")
        : (document.body.style.paddingTop = `${
            this.domSelector(".header-rwd").offsetHeight
          }px`);
    },
  },
  created() {
    window.innerWidth <= 414 ? (this.rwdState = true) : (this.rwdState = false);
  },
};
