<script>
import $ from "jquery";
export default {
  data() {
    return {
      collect: [],
      collectTemp: [],
      rwdState: false,
      rwdStateOnPad: false,
      searchText: "",
      searchCount: 0,
    };
  },
  mounted() {
    setTimeout(() => {
      if (this.rwdStateOnPad == true) {
        $(".board").addClass("justify-content-end");
      } else {
        $(".board").addClass("justify-content-center");
      }
    }, 100);
  },
  methods: {
    getCollect() {
      this.collect = JSON.parse(localStorage.getItem("item")) || [];
      this.collectTemp = this.collect;
      if (this.collect.length == 0) {
        alert("您未加入任何收藏影片");
        this.$router.push("/");
      }
    },
    goVideo(url, id) {
      let postUrl = url;
      let videoItem = [];
      videoItem = this.collect.filter((key) => key.videoID == id);
      this.$router.push({ name: "video", params: { postUrl, videoItem } });
    },
    searchInCollect() {
      let filterArray = [];
      if (this.searchText == "") {
        this.searchCount = 0;
        this.collectTemp = this.collect;
      } else {
        this.collect.filter((key) => {
          let filterText = key.videoTitle
            .toLowerCase()
            .match(this.searchText.toLowerCase());
          if (filterText != null) {
            filterArray.push(key);
          }
        });
        this.collectTemp = filterArray;
        this.searchCount = filterArray.length;
      }
    },
    deleteCollect(id) {
      let pos = null;
      let filterArray = [];
      this.collect.forEach((key, index) => {
        if (key.videoID == id) {
          if (confirm(`確定要刪除 ${key.videoTitle} 這部影片嗎？`) == true) {
            filterArray.push(key);
            pos = index;
            if (filterArray.length != 0) {
              alert("此部影片已從您的影片收藏庫中刪除");
              this.collect.splice(pos, 1);
              localStorage.setItem("item", JSON.stringify(this.collect));
              this.getCollect();
            }
          } else {
            alert("無更動影片收藏庫");
          }
        }
      });
    },
  },
  created() {
    window.innerWidth <= 768 ? (this.rwdState = true) : (this.rwdState = false);
    window.innerWidth > 414 && window.innerWidth <= 768
      ? (this.rwdStateOnPad = true)
      : (this.rwdStateOnPad = false);
    this.getCollect();
  },
};
</script>